/* CSS Custom Properties
–––––––––––––––––––––––––––––––––––––––––––––––––– */
:root {
  --text: Roboto, Helvetica, Tahoma, Verdana, Arial, sans-serif;
  --headings: Oswald, Helvetica, Tahoma, Verdana, Arial, sans-serif;
}

/* Start 
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* Major Second
–––––––––––––––––––––––––––––––––––––––––––––––––– */
p, li, td, th {
  font-family: var(--text);
}

p {
  margin-bottom: 1rem;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
  margin: 3rem 0 1.38rem;
  font-family: var(--headings);
  font-weight: 400;
  line-height: 1.3;
}

h1, .h1 {
  margin-top: 0;
  font-size: 1.802rem;
}

h2, .h2 {
  font-size: 1.602rem;
}

h3, .h3 {
  font-size: 1.424rem;
}

h4, .h4 {
  font-size: 1.266rem;
}

h5, .h5 {
  font-size: 1.125rem;
}

small, .text_small {
  font-size: 0.889rem;
}